import { useState } from 'react'

const LowCode = () => {
  const [gridCols, setGridCols] = useState<number>(12) // 
  const [gridRowHeight, setGridRowHeight] = useState<number>(30) // 

  return {
    gridCols,
    setGridCols,

    gridRowHeight,
    setGridRowHeight
  }
}

export default LowCode
